import React from 'react';
import { makeStyles } from '@material-ui/styles';
import { Grid, Card, CardContent, Typography, Avatar, Tooltip } from '@material-ui/core';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import GetCount from 'crud/GetCount';

import {
  ThisLinePlot
} from './components';

const _ = require("lodash");

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  }
}));

const LinePlot = () => {
	const classes = useStyles();
  	const timeUrl =  'https://covid19.th-stat.com/api/open/timeline'
	const timeData = GetCount(timeUrl)
	const data = timeData.Data  
	const updateOn = timeData.UpdateDate
	const getOutcome=()=>{
	    let thisOutcome=timeData.Data?timeData.Data:[]
		console.log(JSON.stringify(timeData.Data))
		console.log(thisOutcome)
		thisOutcome.map((row, key)=>{
			thisOutcome[key].recoverRate= row.Recovered*100/(row.Recovered+row.Deaths)
			thisOutcome[key].deathRate= row.Deaths*100/(row.Recovered+row.Deaths)
			console.log(key, thisOutcome[key].recoverRate)
			}
		)
	return (thisOutcome)
  }
  
  const outcome = getOutcome()
	
  return (
    <div className={classes.root}>
		<Grid container spacing={2}   direction="row"  justify="center" alignItems="center">
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart1'
			thisName={'Trend of Confirmed, Recovered, Hospitalized Cases'} 
			data={data} 
		    plotLine={{"Confirmed":"#8884d8","Hospitalized": "#50556C", "Recovered":"#82ca9d" }} 
			thisDot={false}
			avatarIcon={{' \u{1f631}':'Confirmed : จำนวนป่วยด้วยโรค COVID-19 สะสม บ่งชี้ถึงแนวโน้มการป่วย', ' \u{1f917}':'Hospitalized : จำนวนรับไว้ในโรงพยาบาล สะสม บ่งชี้ถึงภาระของโรงพยาบาล', ' \u{1f6cf}':'Recovered : จำนวนหายจากโรค สะสม บ่งชี้ถึงผลลัพธ์การรักษา'}}
		/>

		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
			  id='chart2'
    		  thisName={'Trend of NewConfirmed, NewRecovered, NewHospitalized Cases'} 
			  data={data} 
			  plotLine={{'NewConfirmed':'#8884d8','NewRecovered':'#82ca9d','NewHospitalized':'#50556C'}} 
			  avatarIcon={{' \u{1f631}':'NewConfirmed : จำนวนป่วยด้วยโรค COVID-19 รายใหม่วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน', ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ', ' \u{1f6cf}':'จำนวนรับไว้ในโรงพยาบาล วันนี้ บ่งชี้ภาระของโรงพยาบาลปัจจุบัน'}}
			  thisDot={false}
		  />
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
			  id='chart3'		  
			  thisName={'Trend of NewRecovered, NewHospitalized Cases'} 
			  data={data} 
			  plotLine={{'NewRecovered':'#82ca9d','NewHospitalized':'#50556C'}} 
			  avatarIcon={{' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ', ' \u{1f6cf}':'จำนวนรับไว้ในโรงพยาบาล วันนี้ บ่งชี้ภาระของโรงพยาบาลปัจจุบัน'}}
			  thisDot={false}
		  />
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
              id='chart4'		  
			  thisName={'Trend of NewConfirmed, NewRecovered'} 
			  data={data} 
			  plotLine={{'NewConfirmed':'#8884d8','NewRecovered':'#82ca9d'}} 
			  avatarIcon={{' \u{1f631}':'NewConfirmed : จำนวนป่วยด้วยโรค COVID-19 รายใหม่วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน', ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ' }}
			  thisDot={false}
		  />
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart5'
			thisName={'Outcome from Mortality and Recovery Rate'} 
			data={outcome} 
		    plotLine={{"recoverRate": "#8884d8", 'deathRate':'#82ca9d'}} 
			thisDot={false}
			avatarIcon={{'\u{1f6cf}':'recoverRate: อัตราการหายป่วยจากโรค COVID-19', ' \u{1f480}':'deathRate: อัตราการเสียชีวิตจากโรค COVID-19'}}/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
			id='chart6'		  
			thisName={'Trend of Mortality'} 
			data={data} 
			plotLine={{'Deaths':'#8884d8', 'NewDeaths':'#82ca9d'}} 
			avatarIcon={{' \u{1f480}':'Deaths : จำนวนเสียชีวิตจากโรค COVID-19 สะสม', ' \u{1f47b}':'จำนวนเสียชีวิตจากโรค COVID-19 วันนี้'}}
			thisDot={false}
			/>	
		</Grid>
		<Grid item >
			<Typography variant="h6">Update at {updateOn}</Typography>
	  </Grid>
	  </Grid>
	  
    </div>
  );
};

export default LinePlot
